.Tab-container {
  margin: 40px 0px 40px 0px;
  width: 61.8%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between

}

.Selected-tab {
  font-size: 24px;
  font-weight: bolder;
}

.Top-logo-container {
  font-size: 24px;
  font-weight: bolder;
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Top-logo-container svg {
  fill: black
}

.Tabs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  /* flex: 0.5; */
  /* justify-content: space-evenly */
}

.Tab {
  font-size: 24px;
  font-weight: bolder;
  color: #aaa;
  cursor: pointer;
  -webkit-transition: ease-in-out 0.5s;
  -o-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
  margin-left: 20px;
}


.Tab:hover {
  color: #666;
}

.Logo {
  height: 24px;
  margin-right: 12px;
}

@media screen and (max-width: 800px) {
  .Tab-container {
    margin: 20px 0px 20px 0px;
    max-width: 800px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction:column;
        flex-direction:column;
    -ms-flex-pack: justify;
        justify-content: space-between
  }

  .Tabs {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 20px 0;
    width: 100%;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly
  }
}



