.App-card-wrapper {
  margin-top: 100px; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap
}

.App-card-container {
  width: 61.8%;
  height: 320px;
  margin-bottom: 120px; 
}

.App-card-container.Whalesper {
  background: -webkit-gradient(linear, right top, left bottom, from(purple), to(blue));
  background: -webkit-linear-gradient(top right, purple, blue);
  background: -o-linear-gradient(top right, purple, blue);
  background: linear-gradient(to bottom left, purple, blue);
  -webkit-box-shadow: 0px 5px 10px rgba(0,0,255,0.3);
          box-shadow: 0px 5px 10px rgba(0,0,255,0.3);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.App-card-container.iit {
  background: -webkit-gradient(linear, left top, right bottom, from(red), to(orange));
  background: -webkit-linear-gradient(top left, red, orange);
  background: -o-linear-gradient(top left, red, orange);
  background: linear-gradient(to bottom right, red, orange);
  -webkit-box-shadow: 0px 5px 10px rgba(255,165,0,0.3);
          box-shadow: 0px 5px 10px rgba(255,165,0,0.3);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

