.App-margin-right {
  margin-right: 20px
}

.App-card {
  position: absolute;
  width: 61.8%;
  height: 420px;
  /* background: green; */
  margin-top: -100px;
  /* padding-top: 50px; */
  overflow: hidden;
}

.App-phone {
  width: 300px;
  position: absolute;
  left: 100px;
  margin-top: 50px;
  -webkit-transition: margin 0.5s;
  -o-transition: margin 0.5s;
  transition: margin 0.5s;
}

.App-phone.Whalesper{
  filter: drop-shadow(0px 0px 5px rgba(0,0,255,0.3));
  -webkit-filter: drop-shadow(0px 0px 5px rgba(0,0,255,0.3));
}

.App-phone.iit{
  filter: drop-shadow(0px 0px 5px rgba(255,165,0,0.3));
  -webkit-filter: drop-shadow(0px 0px 5px rgba(255,165,0,0.3));
}

.App-phone.App-phone-right {
  width: 300px;
  position: absolute;
  left: auto;
  right:100px;
  margin-top: 50px;
  -webkit-transition: margin 0.5s;
  -o-transition: margin 0.5s;
  transition: margin 0.5s;
}

.App-card-text-container {
  margin-left: 400px;
  width: calc(100% - 400px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}

.App-card-text-container.Left {
  margin-left: 0;
  width: calc(100% - 400px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}

.App-card-title {
  font-size: 48px;
  font-weight: bolder;
  color: white;
}

.App-card-secondary-title {
  font-size: 36px;
  /* font-weight: bolder; */
  color: white;
  opacity: 0.618;
}

.App-card-container:hover .App-phone {
  margin-top: 10px;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}


