.Guy-wrapper {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Guy-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  /* max-width: 61.8%; */
  margin: 20px auto;
  /* background: #a1a */
}

.Guy-photo {
  width: 150px;
  height: 150px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 75px;
  margin-right: 20px;
}

.Guy-info {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* background: #1a1 */
}

.Guy-name {
  font-size: 24px;
  font-weight: bolder;
}

.Guy-divider {
  height: 1px;
  background: #eee;
  width: 100%;
  margin: 10px 0;
}

.Guy-title {
  font-size: 16px;
  color: #aaa;
  margin-bottom: 10px
}

.Guy-email {
  font-size: 16px;
  color: #aaa
}

@media screen and (max-width: 800px) {

  .Guy-photo {
    margin-right: 0px;
    margin-bottom: 10px;
  }
  
  .Guy-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /* max-width: 61.8%; */
    margin: 20px auto;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    /* background: #a1a */
  }
}


